<template>
  <v-card>
    <validation-observer
      ref="observer"
      v-slot="{ invalid }"
    >
      <v-card-title>
        Add new Cold Storage
      </v-card-title>

      <v-card-text>
        <v-alert
          v-model="alert.isOpen"
          border="left"
          :type="alert.type"
          :color="alert.color"
          dismissible
        >
          {{alert.message}}
        </v-alert>

        <validation-provider
          name="Cold Storage Identifier"
          v-slot="{ errors }"
          rules="required"
        >
          <v-text-field
            label="Cold Storage Identifier"
            v-model="form.name"
            :disabled="isSaving"
            :error-messages="errors"
          />
        </validation-provider>
      </v-card-text>

      <v-card-actions>
        <v-spacer />

        <v-btn
          color="success"
          :disabled="invalid || isSaving"
          :loading="isSaving"
          @click="save"
        >
          Save
        </v-btn>
      </v-card-actions>
    </validation-observer>
  </v-card>
</template>

<script>
import {ValidationObserver, ValidationProvider} from 'vee-validate'
import axios from 'axios'

export default {
  name: 'CsmColdStorageForm',

  components: {
    ValidationProvider,
    ValidationObserver,
  },

  data: () => ({
    alert: {
      isOpen: false,
      type: 'success',
      message: 'Outgoing vaccine log saved',
      color: 'success',
    },
    form: {
      name: ''
    },
    isSaving: false,
  }),

  mounted () {
    this.fetchStorageUnits()
    this.fetchTeams()
  },

  methods: {
    async save () {
      this.isSaving = true
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        await axios.post(
          `${apiHost}/storage-units`,
          this.form,
          config
        )

        this.successAlert()
        this.form.name = ''
        this.$refs.observer.reset()

        this.$emit('created')
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        } else if (error.response.status === 400) {
          this.alert.type = 'error'
          this.alert.color = 'error'
          this.alert.message = error.response.data.message
          this.alert.isOpen = true
        } else {
          this.somethingWentWrongAlert()
        }
      }

      this.isSaving = false
    },

    somethingWentWrongAlert() {
      this.alert.type = 'warning'
      this.alert.message = 'Something went wrong. Please contact your IT admin.'
      this.alert.isOpen = true
    },

    successAlert() {
      this.alert.type = 'success'
      this.alert.message = 'Form successfully submitted'
      this.alert.isOpen = true
    },
  }
}
</script>
