<template>
  <v-card class="mx-auto">
    <v-card-title>
      <v-row>
        <v-col cols="9">
          Vaccines in Cold Storage
        </v-col>

        <v-col cols="3">
          <v-select
            label="Cold Storage filter"
            v-model="storageFilter"
            item-value="id"
            item-text="name"
            :items="storages"
            @input="storageFilterUpdate"
          />
        </v-col>
      </v-row>
    </v-card-title>
    
    <v-card-text>
      <v-data-table
        :headers="headers"
        :items="filteredItems"
        :loading="isLoadingItems"
        :search="search"
        item-key="id"
      >
      </v-data-table>
    </v-card-text>
  </v-card>
</template>

<script>
import axios from 'axios'

export default {
  name: 'CsmColdStorageStateTable',

  data: () => ({
    headers: [
      { text: 'Manufacturer', value: 'manufacturer', filterable: false },
      { text: 'Serial', value: 'serial' },
      { text: 'Lot number', value: 'lot_no' },
      {
        text: 'Amount',
        value: 'amount',
        filterable: false
      },
    ],
    alert: {
      isOpen: false,
      type: 'warning',
      message: 'Something went wrong. Please contact your IT.',
      color: 'warning',
    },
    items: [],
    storages: [],
    isLoadingItems: false,
    storageFilter: undefined
  }),

  computed: {
    filteredItems () {
      return this.items
    }
  },

  mounted () {
    this.fetchStorageUnits()
  },

  methods: {
    async fetchStorageUnits () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/storage-units`, config)
        this.storages = data
        this.storageFilter = data[0]?.id
        this.storageFilterUpdate()
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
          return
        }
        this.somethingWentWrongAlert()
      }
    },

    async storageFilterUpdate () {
      if (!this.storageFilter) return
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(
          `${apiHost}/vaccine-logs/storage-units/${this.storageFilter}`,
          config
        )

        this.items = data
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        }
        this.somethingWentWrongAlert()
      }
    }
  }
}
</script>

